export enum ContentType {
  ILLUST,
  COMIC,
  NOVEL
}
export class BaseInfo {
  private _title: string;
  private _icon: any;
  private _show: boolean;
  private _text: string;
  
  constructor(title: string, icon: any, text: string){
    this._title = title
    this._icon = icon;
    this._show = false;
    this._text = text;
  }

  get title(){
    return this._title;
  }
  set title(t: string){
    this._title = t;
  }
  get icon(){
    return this._icon;
  }
  set icon(i: any){
    this._icon = i;
  }
  get show(){
    return this._show;
  }
  set show(s: boolean){
    this._show = s;
  }
  get text(){
    return this._text;
  }
  set text(t: string){
    this._text = t;
  }

}

export class IllustInfo extends BaseInfo {
  private _src: any;
  readonly type = ContentType.ILLUST;

  constructor(title: string, icon: any, text: string, src: any){
    super(title, icon, text);
    this._src = src;
  }

  get src(){
    return this._src;
  }
  set src(s: any){
    this._src = s;
  }
};

export class ComicInfo extends BaseInfo {
  private _pages: any[]; // image src
  readonly type = ContentType.COMIC;

  constructor(title: string, icon: any, text: string, pages: any[]){
    super(title, icon, text);
    this._pages = pages;
  }
  get pages(): any[]{
    return this._pages;
  }
  set pages(p: any[]){
    this.pages = p; 
  }
};

export class NovelInfo extends BaseInfo {
  private _rb = "";
  private _illust: any;
  private _text2: string | undefined;
  readonly type = ContentType.NOVEL;

  constructor(title: string, icon: any, text: string,
     rb: string, illust?: string, text2?: string){
    super(title, icon, text);
    this._rb = rb;
    this._illust = illust;
    this._text2 = text2;
  }
  get rb(){
    return this._rb;
  }
  set rb(r: string){
    this._rb = r
  }
  get illust(){
    return this._illust;
  }
  set illust(i){
    this._illust = i;
  }
  get text2(): string | undefined{
    return this._text2;
  }
  set text2(t: string | undefined){
    this._text2 = t; 
  }
};