export const novel = `　部屋へと連れ戻した医者の手が肩から外れても、彼は顔をあげなかった。ベッドへ深く座り込んだまま、何も言わずに頭を覆っている。
　医者も無駄口は叩かずに、ただ彼が落ち着くのを待った。しばらくして、青年が呟く。
　「……もう一人、いるんですね？」
　漏れそうになった声を飲み込み、サラテリは静かに頷いた。
　「"あいつ"にあんなことのできるはずがない。それに、うまく覚えてもいない。…俺は、こんなこと、望んでなんか……」
　「勿論だとも。気に病む必要はない。君が悪い訳じゃないのだから。」
　「へえ、じゃあおれが悪いっての？」
　声音が一変すると今度は試すような笑みを見せる。医者は驚きも見せずにただ質問した。
　「……君にも、何らかの動機がありそうだね。無差別だけど、無意味ではなさそうだ。」
　「はは、あんた変な奴だな。」
　彼は手に重心を乗せて後ろに傾く。ほんの少しだけ、弾むように足を動かしていた。
　「……はじめまして、になるのかな。私はサラテリ。君は？」
　「名前がほしーなら、テキトーに呼べば？どうせ区別できれば良いんだろ。」
　遠回しがいけないのかと直接聞いてもこの反応だ。どうしたものかと医者が頭を悩ませていると、閉じているだけの扉が勢いよく開いた。
　「ウェルド！」
　「兄さん、大丈夫…？」
　駆け入ってきたのはシアンとグラナートだった。スクイルが終わる頃は過ぎていたものの、常の彼らが帰るのにはまだ幾らか早い。おそらくは義務以外はキャンセルして飛んできたのだろう。
　「グラナート、シアン……？」
　彼の声にも優しさが戻っていた。
　やや遅れて医院者が一人入ってきて、サラテリに止めたのだがと言い訳する。そのことは特に咎めず、少し距離をおいて彼らを見守った。
　「事件に巻き込まれたってきいて、けど、良かった……」
　「悪い、心配かけて。俺は大丈夫だから。」
　感極まって抱きつく妹の頭を青年は申し訳なさそうに優しく撫でる。シアンもほっとしたのか涙を浮かべて心配したのだと怒っていた。
　「兄さん、一体何があったの…？」
　しかし落ち着いて息を整えたグラナートの問いに、彼の顔から宥めるような笑顔は消えた。勘の良い彼女は、すぐにそれに関するのだと気づいて無関係のシアンをちらと見た。
　「……いや、シアンにはもう隠してられないな。……なあ、多重人格ってわかるか。」
　「え？……うん、まあ。話に聞いたことくらいは。けどどうしてそんなことを……まさか。」
　「そうさ。俺はそれに似たようなもんだ。たぶんお前らが想像してるのとはちょっと違うんだが……」
　シアンはその説明に、それ以前の暴露にしばらく目を白黒させていたが、今はその状態の真偽を確かめる必要はないと先の質問をした。
　「じゃあ、事件ってのも君じゃない君…が関係するのか？」
　グラナートがきっとシアンを睨む。何かまずかったのかと少年がびくりとしたのに気づいてウェルドは妹へ向き直った。
　「いや、"あいつ"じゃないんだ…もう一人、できちまったみたいで…」
　冗談にしたいのか彼は苦し紛れな笑みを作る。特に、彼が嫌いなことを知るグラナートには、ウェルドの気持ちが直に伝わってくるようで、どうしようもない感情を地へ送る。話についていけないシアンが更なる説明を求めた。
　「……ええと、随分前からそうだったの？それに、今回の事は…」
　「シアン…君と言ったかな。」
　が、今まで成り行きを見るだけだった医者が、意気込む姿勢にぴくりと反応した青年を見逃さずに口を出した。いつの間にかもう一人の医院者は部屋を後にしていた。
　「…悪いが、ウェルド君は疲れているようだ。大方の経緯や事情なら私でも説明できる。部屋を移そう。…妹さんはどうする？」
　少女は感心した表情のまま、少し話したいと頷く。医者も彼らの仲は知っている。おそらくはさほど負担にならない―むしろ、落ち着く手助けになるかもしれないと笑顔を戻す。
　シアンはやや不服そうに、しかし心配なのは同じなのだろう、友に無理はするなよと告げて医者と共に部屋を出ていった。
　足音が聞こえなくなるとグラナートはひとつため息をついた。
　「……どうした？」
　「ううん。ちょっと安心しただけ。兄さんは大丈夫？」
　「俺は問題ない。」
　少女が静かに寝床へと腰かけ直すと、習ってウェルドも姿勢を正した。彼女は前方の、何もない扉を無意味に見つめ続ける。
　「……グラナート。」
　「何？」
　「…悪い。色々ありすぎた。これからもどうなるかわからない。」
　「兄さんが謝ることじゃないよ。それより、本当に無事で良かった。はじめは怪我したんじゃないかって、とてもしんぱい、したんだから……っ」
　彼女にしては珍しく感情から言葉を詰まらせた。ウェルドは妹を大切にしていたが、グラナートもまた、兄を何よりも想っている。ただ人前を気にして冷静さを保っていただけだったのだ―
　
　「じゃあ、僕と出会ったときは既にそうだったんですね……。」
　少年が悔しそうに目を細めたのに医者は誤解を解くべく補足する。
　「ウェルド君はもう一人の彼…シャトルーズ君を良く思っていないようでね。誰かに話す以前にその話をする事自体避けていたようだから。」
　「あ、ごめんなさい。そういうんじゃないんです。…今思えば、あの時から様子が変だったのに…僕は、こんなことになるまで気づきもしないで。」
　シアンは邪魔なものを吐き捨てるように息を思い切り出すと、よし、と前を向く。
　「できるだけ、やれることをしようと思います。…そりゃ、義姉さんやグラナートさん程元気付けられないとは思うけれど…」
　「いや、友人の存在は嬉しいものだよ。是非、これからも会ってあげてほしい。」
　シアンは瞳に強くて危うい光を灯し続けたまま頷いた。
　
　サラテリは目を閉じ、溜め息を深呼吸に擬態させて自身を一度落ち着かせた。そうして開いた視界の先には顔を両手で覆って項垂れる青年の姿が、周りの負の視線にさらされていた。彼は他の医院者や手伝い人に被害者の他目撃者らへも注意を彼からそらすよう対応してほしいと指示を出すと、ゆっくりウェルドの元へと歩いていく。差し出された手に、それでも彼は首を振ると一人静かに立ち上がる。
　「部屋へ戻ろう。」
　決められた物の並ぶ殺風景な部屋に帰っても、彼は座りもせずにただじっと窓の向こう側を望んでいた。硝子戸に映る無表情にサラテリは不安を覚えながらも、事務的に質問を続ける。
　「落ち着けたかい。」
　「………………」
　ウェルドは悩んでいた。自分の感情がうまくわからないのだ。口ではそんなはずはないと言っているものの、その実"自分"は何を感じ、何を想うのかがはっきりしない。今まで当たり前だった"おもしろいこと"。それは、本当にそうだったのだろうか。本当に、悲しいことだったのだろうか…？
　「ま、少しは…ってとこかな。まだまだ足りねぇけど。」
　反射する表情がへらりと笑う。深刻さはどこへやら、彼は振り返ると挑発するように重心を移して腰に手をあてる。
　「君は、」と言葉を続けようとした医者だが、すぐに"戻った"不安を見ると続きは飲み込んだ。彼は目があったことへ動揺して顔を背けるが、考えても答えはでないことを悟ったのかようやく口を開いた。とはいえ、出てきたのはその場しのぎの文句だったのだが。
　無理はしないようにと決まり事を告げ、青年の言葉どおりに医者は部屋を後にする。腹のうちでは面倒な奴と思われているのだろうなどと下らない考えだけがはっきりとしていた。
　「……"シャトルーズ"。
　…なんだ。」
　記憶を共有する彼にとって、己と会話することに昔から難はなかった。自分が何を言ったかを思い出せばいい。けれど。
　「どうして、新しい奴のときの記憶はないんだ？
　…………」
　口は動かない。考えも変わらない。
　彼にもわからないということなのだろうか。それとも。
　「もしかしてお前は、俺の記憶を操…
　フフ…俺がそのようなことのできるはずもない。単に《あれ》はそこまでお前ではないというだけだろう。」
　まるで打ち切るような、彼にしてはやや強引なやり方にウェルドは機嫌を損ねる。が、すぐにどっと疲れが押し寄せて窓際の机に肘を押し付けた。
　「……大丈夫か。
　これくらい、なんとも……」
　ひどく眠いような気だるさ、これがようやく落ち着いて思い出しただけならばいいのだが…。意識を保つのがやっとであった。
　「しばらく休め。その間は…
　お前が代わりをやるって？勝手に暴れられるのはごめんだが、俺はお前だって…まだ……」
　やがてその体勢も辛くなって、なんとかベッドまで身体を運ぶ。整えられたシーツも構わずに座り込むと、ただ呆とまた外を眺めた。
　逃げたり飛び降りたりを危惧してだろう、開かない窓は風の音さえ聞かせてはくれない。ここから見える木々の仰け反りで想像するのみだった。
　あんなに興味もなく、行くのも面倒であった学校が、今となってはひどく恋しい。進段の心配をしていたころが懐かしい。
　…傷つけてしまった友達は、元気だろうか。シアンはああして来てくれたけれど、彼は被害者ではない。彼らがどの程度の負傷で、何を思ったのか。…死人がでたのか、きっと尋ねても真実は教えてもらえないだろう。
　ウェルドは自らの両手をそっと握りしめた。
　
　暇だろうとグラナートが気をきかせて持ってきてくれたものやトレーニングがあったりでも、さすがに時間を潰すには限度があった。せめて読書の趣味でもあればなどと思うのだが、生まれてこのかた本など読み終えたこともない。もう長いことここで過ごしているようで、実際には1灯とて過ぎてはいない。暇というのは時を遅く感じさせる。
　これからどうするのだろうか。よもやこのままというわけにもいくまい。"それ"は相変わらず勝手に暴れて、制御できなければ自由に外へ出ることも叶わない。
　そんな青年の呑気な欲などいざ知らず、ファッジュでは抗議が起こっていた。
　「いい加減にしてください、院長。これじゃ悪化するばかりです。彼は施設に入れるべきだ。」
　まくしたてるようにサラテリへ抗議するのは、アエンカという青年だった。彼だけではない、周りのものも結論を求めるように横目にうかがっている。
　「アエンカの言うとおりですよ。……シラアイさんは、もう退院できるほど良くなっていたのに、あの子のせいで意思さえ保てなくなってしまった。」
　「せめて戸締まりは再度確認してくださいよ。」
　ざわざわと部屋が騒がしくなるが、そのほとんどが同じ方に向かっていた。矢先の医者は謝罪で口を濁したが、鍵を閉め忘れたことはおろか、確認を怠ったことさえないのになと不審を秘める。
　医院者からのウェルドの評判は頗る悪かった。そのような近況で予定を変更しなくて良いものかと悩むのだが、さすがにおいそれと外せる用でもなく、考えるだけで終わってしまう。
　「どうかしたんですか？」
　「ああ、いや……近々院集会がありましてね。本は見つかりました？」
　「院長って大変ですね。ええ、これです。手続きをお願いします。」
　医者は指された背を摘まんで取り出すと、そのままカウンターまで向かって担当者に本と紙切れを差し出す。
　本館二階には入院者向けの図書室がある。患者か医者、手伝い人ならば誰でも自由に本を借りることができる。広さもあり、休憩所も兼ねていたため利用者は多かった。借りるでなければ見舞いに来た一般人も入ることはできる。とある事情から一人では借りられない患者に付き添ってサラテリはそこへ訪れているのだった。
　情報整理音を待ちながらふと奥の棚をうかがう。高くそびえる可動式の棚に興味のあるものは比較的少なく、ぱらぱらと人がいるかいないかという景色であった。
　癖のようにあの人は元気そうだとか無意識に確認し、視線を戻そうとして見えた人影に振り返る。処理を終えて本を渡そうとした係りの者に驚かれて我に返り、何でもないと苦笑いして受け取り、患者へと手渡す頃にはもはやどこにも見受けられなかったが、見間違いとも思えずに心の片隅に寄せておくのだった。
　―彼が、ここにいるはずはないのに。
　
　ウェルドは意識を失って尚子を守るように抱える者の前で立ち尽くしていた。その頬は赤黒く汚れていたが、視点が定まらぬ表情が、それは彼自身の傷でないことを物語っていた。辺りには幼子の鳴き声が響いている。
　駆けつけた医院者が避難を誘導しながら牽制したが彼は顔を向けることさえしない。
　「…どうかしたのかい。」
　「…わっかんね。うーん、殺せばいいのかな、やっぱ。」
　人物に目星をつけつつサラテリが静かに問う。受け答えこそすれ、相変わらずつかみどころのない口調であった。
　足元に散らばった―おそらくは入ってきたときに割ったのだろう―ガラスの欠片が目を光らせている。
　「そんなことをしても、君は満たされないだろう？」
　「あんたにわかるか？」
　「何かを傷つけて得られるものなどないよ。」
　「さあて、どうかね。……少なくともあいつは傷つけるとよろこ」
　向き直りもせずぼうと前方を見つめたままであったが、不自然に声が止まると青年は目をつぶって一度笑うように息をはき、くるりと医者へ向き直った。
　「なあ、ダファディルってのはどうだろうか？なかなかいいセンスしてると思うんだけどなー。」
　突然なんの話やらと眉を寄せたのに気づいてか彼は今までで一番全うな会話をした。
　「おれの名前！便利だろ。」
　「…自分で決めたのかい？」
　「勿論。あいつらがつけてくれるはずもないからな。」
　自惚れるように得意気に何度か己が名となる言葉を繰り返す。医者は一度だけ周りには誰もいないことを改めて確認すると会話を続ける。
　「君は彼らのことが嫌いかい。」
　「は？何でそう思うの？」
　「うまくやっていない気がしてね。」
　ダファディルはむっと口を寄せる。
　「…ウェルドのことは好きだぜ。シャトルーズは、まあ、よくわからん。向こうがおれを良く思ってないだけだって。」
　「それは君がこういうことをするからだろう？」
　「…………さてね。」
　青年は両手をあげわざとらしい伸びをする。その腕が下がる頃には笑みも消えていた。
　「……すみません、俺、またこんなとこに来てるんですね。」
　挑戦していく輝きを完全に失った瞳で彼は自ら部屋へと歩みを進めるのだった。
　
　「……どういうことだ？」
　呟かれた言葉は聞き飽きるほど慣れない自分の声であった。とはいえ近頃はそれも増えたなとまた彼は光を失う。
　「先生は忙しいからしばらくはここにいないってこと。」
　それでも先程までがそうであったように、他人の前ではそれなりを繕っているのだが。
　「それを伝えに来たのか。
　そ。直接回って話しながら状況も確認してるんじゃねーのか？」
  ウェルドは面倒くさそうに天井を仰ぎながら答える。周りには誰もいないから、大きな独り言もさして恥ずかしさはないが、どうしても彼を好きにはなれない。かといってもう一人のそれがましとも思えぬが。
　「…少々不安だな。
　へえ、お前が人を信用してたのか？」
　済ました顔で首を振る。すぐに口をゆがめたが、その何とも言えぬ心地悪さは拭えなかった。
　「あの医者は頭領なのだろう？目の届かぬ所ならと考えるのが下につくものの常だ。
　ふーん、知った風な口調だな。…なんだか最近変だぞ、お前。」
　ウェルドにとってシャトルーズとは、ただいるだけの存在であった。どちらかというと気分に近かった。何をするわけでもない、そもそも何もできぬと思っていたのだが。まるで何もかも知っているかのような口振りが多い。始めのうちこそ他人の手前強がっているだけかと気にしなかったが、どうもそうでないような気がする。
　「買いかぶりすぎだ。俺はお前ほど器用ではない。せいぜい、代わりに立つことくらいしかできないさ。」
　すとんと落ちるように自分だけを取り戻す。眠ったという例えが面白いだろうか。
　今でも、自分でもわかる簡単なことが理解できていないようであるのに、反面自分では思ってもみなかったことに気づいているようだ。正直なところ、悔しくも確かにと思ってしまう。周りからの評価など人並み以下にしか気にしていなかったが、考えてみれば自分など厄介でしかない。事実、主治医のサラテリ以外は大した会話をした覚えがない。そもそも部屋から出るなとは言われているが。
　「頼むから暴れてくれるなよ、俺……」
　
　しかし、そのような上辺な儚い想いなど、彼が受け取るはずもなかった。
　青年は机の上に立ち、おそらくは客であろう者の顔を掴んでいた。苦しそうにもがく足がぶらぶらと揺れている。
　「こら、離しなさい！……ったく、言わんこっちゃない。おい、今朝食事を届けたのは誰だ？」
　そこは本館にある一般食堂であった。既に多くの医院者が集まっていたが、周囲のパニックの対処やらで元凶を抑え込めずにいる。
　「そ、それが…きちんと閉めたんですよ。数人で確認もしました…」
　「別館ともなれば脱出も易くはないはずなのに…」
　「そんなことは後だ！今はあの人を助けないと…！」
　誰かが狂った笑みを浮かべてその場を離れた。
　「ふふ、たのしい？ねえ…」
　「ふざけたことを言っていないでその人を離しなさい！」
　「おれはオーマジメだぜ？」
にこにこと無邪気な笑みからも調子に乗ったような口調からもそのようなことが信じられるかと舌打ちが聞こえた。
　「うーん、……そっか。」
　停止した空気を破って青年はぽいと持っていた者を放る。落ちてきた被害者を慌てて受け止めようとして幾人かが潰れた。
　勢いづいて追いかけようと、道を塞ごうと彼は軽々かわして、医院者同士がぶつかる始末。とはいえ彼らも闇雲に突っ走っていたわけではない。"本気で捕まえようとしている"体が欲しかったのだ。
　やがてとある人物が戻ってくる。手には何やら厳重管理されたような鞄をさげ、青年から少し離れてそれを開ける。
　「……あー、もう、わかった。降参だ。好きにしていいから。」
　その様子をちらと確認して先頭にいた医者が軽く手をあげる。目までつぶって油断を見せたのにウェルドは拍子抜けしてしまう。
　「なんだ、もういいのか？案外薄情だねぇ」
　「何とでも言え。もう私らは諦めるよ。」
　器用に掴んでいた天井の窪みから手を離し、青年はぽんと机上に飛び降りる。周りのものは下からただ見つめるだけのようだ。
　彼はひとつ頷くと、そのまま床へと足場を移す―そのときだった。
　それまで気も力も抜けたような様子だった医院者らが一斉に動きだし、彼の肩や足を捕らえる。驚いて一瞬だけ成されるまま身動きを封じられ、ちくりと何かが当たって、我に返ると抑えていた彼らの手を振り払って再び机へと飛び乗る。
　「っ……てめえら、だまし……」
　ウェルドは怒りの形相で一番近くの医院者を睨み付けるが、すぐに焦点が定まらなくなって、足元がふらついて落っこちた。
　「…？？？/＆＊＊^！>$？？％<$！？」
　頭を抱えて奇声を発し、力なくその場でじたばたと暴れている。そんな様を涼しい顔で見つめてやれやれとため息をつく者もいれば、ようやく一仕事終わると気を緩める者もいた。
　「…全く、始めからこうすりゃ良かったんだよ。院長は優しいというより甘すぎるな。」
　「おい、誰かこいつを部屋へ運べ。残りの者はこの部屋を片すぞ」
　青年は手足こそ動かしていたが、掴めば容易に抑えられるほど周りを認識していなかった。手伝い人が言われた通り、いつもの不服顔でコーリカマスへと詰め込んでいく。
　彼の感じた違和感は「チンセイザイ」という薬によるものであった。早い話、意思や心といわれる主観を一時的な混沌へと叩き落とす代物で、うたれると思考や感情が分別なく入り乱れ、自己防衛のために意識を手放す。そうして強制的に鎮めることができるのだ。身体的な後遺症はないとされているが、その経験は心に傷を負わせ、加えてその苦痛は見ているだけで容易に想像がついてしまう故に乱用は禁止されていたが、今回使用を非難する者はいなかった。それだけ”彼”は彼らにとって害悪と見なされていたのだ。
　別館へとやって来た手伝い人らは乱暴にウェルドをベッドへと放ると鍵をかけてそそくさと部屋を出ていく。もはや彼の意識は悪夢ごと奥底へと沈み込んでいて、何も知らぬ彼が呑気にあくびをした。
　ウェルドはどこかだるい感覚を誤魔化そうと頬を叩いてみたりぴょんと跳ねたりしてみて、違和感に首をかしげたが、一向に解答を得られそうにない。さきほどまで一体何をしていただろうか。いつの間にか眠ってしまったのか、それとも……
　これ以上考えを巡らせても仕方ないかと青年は窓際にやや長く伸びる机の引き出しに手をかける。既にいくつかの物が整理されないながらに収まっていた。それらを無造作に机上へと取り出し、無意識のような手馴れた様子で"所定の"位置へ並べていく。理解できぬと眉をひそめたのも塗りつぶして、ペンを握るのだった。

　配膳係の手伝い人は覚悟を決めて扉を開けた。係は順番に回っていたが、何もこのタイミングでなくともと巡り合わせを恨む。
　しかし予想に反し、隔たりのなくなった先を覗いても、そこに青年の姿は見受けられなかった。遮光布が部屋を薄暗く不気味にさせていたが、それ以外はひどく平穏なものであった。不思議に思うも、幸運と思い持ってきた物を奥の机へと置き、さっさと帰ってしまおうと振り返る。彼はそこにいた。
　「よくもっ･･･！」
　手伝い人の口から悲鳴が上がるより早く、ウェルドは彼女の頬を殴った。叫びは鈍い苦痛の音に変わる。近くに人はいない。彼が続けて首をつかむと、振り上げた反対の手に握る銀を僅かに差し込んだ空の明かりが照らし、目を丸くした手伝い人は慌ててじたばたと暴れたが、怒りを交えたその笑みにやがて抵抗もできなくなる。刃物の先が首筋に当たり汗が垂れる。
　「っ………う…るさい……」
　が、それ以上深く、刺さる前にウェルドは身を引いた。頭を押さえるようにぶつぶつと何かを言っていたようだが、彼女にとってはどうでも良いことだった。部屋を飛び出し、一目散に本館への階段へ駆け寄り外へと出ていった。
　「……あいつ等は、おれたちにヒドイコトしたんだぞ？！」
　床へと座り込んだ彼は刃を持たない手で頭を押さえたまま哀しそうに声を荒げる。
　「それはお前がふざけたことをするからだ…！」
　受け答えるのも彼自身の声だった。彼はいつの間にか"目の前"にいた。
　「何を…おれはただ、お前の」
　「聞きたくない！」
　目も開けられずにふらふらと膝で立って頭を振る。
　「お前たちなんか…嫌いだっ…お前らなんか…
…ウェルド……
消えてくれ、頼むから…」
　ことりと物音がすると、青年は両の肩を反対の腕で押さえ深呼吸する。扉からの廊下の照明にも、窓側の布の隙間からの光へも、どちらもすがる気にはなれない。
　やがて廊下の光が遮られるのだが、彼はそのことも覚えてはいなかった。
　
　「ああ、面会ね。ほら、どうぞ。くれぐれも施錠は忘れずに。」
　無造作にカウンターのテーブルに鍵を放られてシアンは訝る。
　「あの、ちょっと乱暴すぎませんか？」
　「え？だって君は顔が知れてるし…場所もわかるだろう？」
　そうしてしばらく押し問答を続けていたのだが、受付の態度は変わらず少年は頬を膨らませながら広告も期限の過ぎている廊下を進んだ。
　「ウェルド、入ってもいいか？」
　「ああ。」
　戸を開け中へ入るとウェルドも不思議そうな顔をする。
　「……お前一人？」
　「そうなんだよ。結構いい加減だよな。場所知ってるなら勝手に行けーだなんて。」
　シアンは憤りをぶつける先を見つけてつい口走るが、すぐに察すると目をそらした。
　「あ…ごめん。こんな話をしにきたんじゃないのに。」
　「いいさ、別に。」
　なんとなく和んで青年は穏やかに苦笑いする。シアンは整理された椅子の中から一つをウェルドの側へ持って行くと、そこへ腰掛けながら話しかける。
　「寝てたのか？」
　「ん…ああ、最近なんだか気だるくて。」
　「そう…まあ、退屈だよね。何かあればいいんだけど…」
　「お前だって忙しいだろ？そろそろ虹催だもんな。」
　シアンは頷いて悲しそうに微笑む。
　「うん…アスだよ。グラナートさんもそれで来られないって。」
　「お前はいいのかよ。」
　「ちょっとくらいね。」
　虹催直前、授業もなく出し物をするクラスが追い込みをかける大切な時間であるのはウェルドも承知していた。それでも見舞いに来てくれる友人に、感謝しながらも本当にお人好しだなと思う。
　「最近どう？”彼ら”は。」
　「え…？いや、まあ…なんとか落ち着いてるかな。」
　ウェルドは咄嗟に、心配させまいと適当な言葉を並べる。やや目を逸らしたおかしな態度ではあったが、シアンは期待からかあまり気にしていないようであった。
　「ほんと？じゃあさ、ちょっとくらい虹催に行けないだろうか？ほら、気分転換にもなるし…」
　けれどもそれは失敗だったなと後悔する。今の自分の立場では誰に頼んでも外出許可の出るはずがない。
　「さすがにそれはどうだろう…ほら、学校で始めに事起こしてるわけだし…」
　「そっか…そうだよね。」
　シアンはすとんと乗り出した身を戻しため息をつく。その瞳に宿った影は、ここに来る少し前の事を思い出したためであった。

　彼はウェルドの暇つぶしになるならと、気が紛れるならと課題や授業用紙でも届けようかと思い立ち、教員室へ向かっていた。勿論親友がそれを面倒くさがっていたことは知っているが、それは間違いなく"これまで"であったからだ。
　定型の礼をして入っていく。普段は関わりのない教師―ウェルドの担任―を見つけると、挨拶をした。
　「こんにちは、僕は４段のシアン・シュプリームと言います。ウェルドの友人なのですが、彼のクラスで何か課題などはありませんか」
　そのようなことを尋ねたのだが相手はきょとんとして瞬きする。
　「…えーと、どうしたんだい？それに、『ウェルド』って…？」
　驚いたのはシアンも同じであった。場に無言(しじま)が続く。
　沈黙を破ったのはその状況に気づいたシアンの担任であった。彼女は慌てているのを悟られないよう静かに駆け寄ると、
　「シュプリーム君？ちょうどよかった。少し来てくれ。いや、すみませんね…ははは」
　半ば強引にシアンを連れていく。彼も戸惑いながらも、返事をするととりあえずその言葉に従った。教員室を出て廊下を歩き、小部屋へと入って一つ息をはくと、くるりと向きをかえて担任は呟いた。
　「ここならいいかな。」
　「あの…」
　「ああ、ごめんね。そういえば君も彼とは仲がよかったんだったね…」
　シアンが説明を求めると、教師は顔つきを変える。嫌なことでも思い出すような酔いしれた表情だ。
　「今、彼のことには触れないでくれ。特に３段の関係者には。…あんなことがあったんだ、皆忘れてしまいたいんだよ。」
　「そんな…まさか、」
　教師は頷くと窓の外を見つめ補足する。
　「彼は学生想いの教師でね、その場にいなかったとはいえ傷ついた子たちを見て悩乱したんだろう。生徒たちも、必死に笑ってはいるけど傷心していると思うよ。」
　「けど、だからって忘れて言い訳が…」
　「わかっているさ。皆頭では。別に彼だけが悪い訳じゃない。それなりの理由もあるだろう。でも、今は…」
　教師は面と向かって微笑む。まるで辛いのは自分も同じだと言わんばかりに。
　そうしてシアンは無理矢理ウェルドの話題を出さないことを約束させられ、例の思いつきもあきらめざるを得なかったのだった。

　「シアン、どうかしたのか。」
　ウェルドに呼びかけられてはっと我に返る。彼はちょっと思い出したことがあるだけだと苦笑いしてやり過ごす。
　「でさ、お前んとこは何やるんだ？」
　「え？うん、演劇をやるよ。」
　内容を知ってげっと慌てたのはウェルドだった。
　「尚更抜け出したらまずいだろっほら、帰った帰った！」
　「大丈夫だって、僕は道具制作の係でもう終わってるから…」
　それでも何かあったらどうするんだとウェルドは帰らせようとする。そんな彼にシアンは俯いて白状した。
　「…ほんと言うとね、少し嫌なんだ。」
　「え？」
　「何事もないように時が過ぎていく。これでいいのかなって、よく思うからさ…」
　項垂れたままぽつりと呟くシアンに友はわざとらしくため息をつく。なんだと彼も思わず顔をあげた。
　「お前はフロスか何かか？出来ることなんかたかが知れてるんだ。どんなに嫌でも、結局はなるようにしかならないさ。」
　彼は”何事もなかった”かのように爽やかに笑っていた。それがひどく無知で哀れで……何よりも強かった。
　「…そうだね、ごめん。」
　「俺の分まで楽しんでこいよ。」
　「ああ。」
　きっと嫌なことはたくさんあるだろうに。思い通りにならないことなど、他人より多いだろうに。シアンは手を振ると、また来るからと音をたてないように扉をゆっくり閉めた。

学習能力が低いのか、それ以上の何かがあるのかなどは医院者らの知ったところではない。ただ目の前に悶え転がる彼を見下し、淡々と仕事をするのみであった。このような目に遭うのはこれで3回目。相変わらず"彼"は暴れることをやめなかったが、それでもはじめの頃より抑えるのは幾らか楽であった。"チンセイザイ"を見せれば反射的に動きが鈍る。数人でかかるだけですぐに取り押さえることができた。あとはこうして鎮めればいい。
　「ほら、暴れただけ苦しむんだ。そろそろやめにしないか。」
　「……～～～～っ-………」
目元を押さえていた手の片方が勢いだけよく医院者の足首をつかむ。歩く程度で振り払えそうなほど弱々しいものであったが、図らずも鬱憤晴らしになりつつあった彼はそうはせず、そのまましゃがむとその腕を掴み返した。
　「おやおや、今回はもう一回いっとくか？」
　「おい、さすがにやめとけよ。」
　「構わんだろ。慣れてきたのかもしれない。」
おそらく止めた者も"彼"の心配などはしていなかったのだろう。それ以上は何も言わず、むしろその様に痛快ささえおぼえていた。
　ちくりと腕に痛みが走る。恨みを抱えていた彼の表情はみるみる青ざめ、声を出すことさえできないようでただ震えていた。―そうして、彼は早急に意識をそのままに奥へと引っ込んでしまったのだ。
　そんな状態とは露知らず、いつものように手伝い人らは荷詰めを始める。慣れたような頼りない手つきで作業を進めながらぼそりと
　「端から動けないようにすればいいのに」
などと愚痴を漏らした。
　「どういうことだ？」
　「ほら、食事に"スイミンヤク"を混ぜるとか…」
　しかしその提案には誰もが乗り気ではなかった。さすがに"基本人格"にまで害を及ぼすことは明らかであったからだ。彼らは"彼"に恨みなどない。
　聞き入れられぬことは見越していたのか、手伝い人らはそれ以上は何も言わずにそそくさと部屋を出ていった。
　揺られる内にも彼は闇の中をさ迷う。果てしなく狭くて息苦しいほど広い空間に押しづめられてひとりぼっち。それが何よりも当たり前の真理なのだと疑うことさえできずに。
　それから"彼"が暴れることはなかった。大人しく部屋にこもり、食事にさえ手をつけようとせず配膳の係りをただ静かに見つめるばかりであった。サラテリが帰ってきたのは、そんな頃であった。
　彼は順々に部屋を回り容態や状態を確認しつつ挨拶をしていく。無論留守を預かっていた医院者らからの報告もあるのだが、それでよしとはしなかった。
　ウェルドのいる部屋は別館に位置することもあり、必然的に最後に訪れることになる。彼はノックと呼び掛けの後に返事がないのを不審に思いながらそっと扉を開ける。眠っているのかもしれないという予想とは裏腹に、彼は寝床の上で静かに座っていた。
　が、医者の姿を認識するなりびくりと跳ねて眼光を鋭くする。顔は両手で覆われ、表情こそ正確に見えなかったが、感情は用意に知ることができた。
　医者は固唾をのみこむと極力刺激しないように優しく話しかけた。
　「久しぶり、ウェルド君。…どうかしたのかい？」
　「……………………」
　あまりいい状態ではないなと髭を撫でていると机の上の冷めた朝食が目に留まった。
　「ああ、食事がまだみたいだね。食べられそうに…」
　「いやだっ…"スイミンヤク"がはいってるんだ……また、おかしくなる…」
　打ち切った言葉にまさか、と医者はかけより、持ち合わせの何やらで確認したが、特に異常はなさそうだと知るとますます状況が悪化していることに冷や汗を流す。
　「…大丈夫、そんなものは入ってないよ。」
　「……？先生、なんでここに……」
　先程までの怯えはどこへやら、青年はすっかり平穏な表情を取り戻して手を下ろす。が、それも束の間、瞳孔を見開くと罵詈雑言と共に頭を抱えうずくまってしまうのだった。
　医者は下手に声をかけるべきではないと一言だけ挨拶すると、受付に面会拒否を依頼してファッジュへと飛んでいき、何やら話をしていたアエンカたちを捕まえて問い詰める。
　「私が留守の間、ウェルド君に何をした？」
　「え、いえ……彼には、何も……」
　曖昧な言い回しにはあ、とサラテリは一つため息をつく。
　「…質問を変えよう。"ダファディル"君は暴れたかい。」
　向かう3人ほどの医者は誰も院長の目を見ようとしない。
　「…どう抑えた？」
　「それは……」
　少しの間はそのまま黙りこんでいたが、ようやく彼らはこれまでの経緯を説明する。
　「……"チンセイザイ"を使用するどころか、一度に2回も投与するなんて…呆れてものも言えないな。」
　「け、けど、あれは意識がなくなれば切れるでしょう！？…それに、あれから大人しくなりましたし……」
　「意識を手放すでなく"交代"したんだろう。大人しくなればいいというものでもないだろうに…」
　ひとまず彼らの処遇は後で考えるとして、状況を理解したサラテリは再び別館へと足を運んだ。相変わらず返事のない扉を開ける。彼はこちらに背を向けて机に向かっているようであった。ノートや筆記具は妹に持ってきてもらったのだろうー売店には売っているが彼では行くことはできないはずであるー淡々と筆記音が部屋に響き渡る。
　医者は深くは入らずにその背に謝罪する。
　「すまない。…酷いことをした。辛かったろう……」
　答えはない。
　「本当に、こんなことになってしまって……」
　かり、と音が途切れたのは彼がようやく医者に気づいたからではなかった。かたりと置かれたペンが少しだけ転がる。
　「信用など、するべきではなかった。『ウェルド』は酷く混乱している。……もう、お前にも"シアン"にも任せない。」
　彼は無を纏いゆっくり席を立つ。言われもない威厳が恐怖を煽り、まっすぐ出入り口へ向かう青年の前に立ちはだかるべきという考えさえ及ばない。なんとか出たのは言葉だけであった。
　「ま、待ってくれ。外に出たら今度は治安維持局が君を…」
　「その程度は問題ない。」
　「え…？」
　真意を確かめるより早く、出ていこうとする"それ"に銀が舞い込んできた。驚きを落ち着け確認してみれば、少女が顔をあげ、遅れておってきたであろう受付係が息を切らしていた。
　「すみません、どうしてもと押しきられて……」`