　散々交わって欲にまみれたあとで再び突き放したのはクノーテンであった。
　彼はそれまでの甘えた様子を駆逐したように冷静な表情を取り戻し、拒絶を示すかのように口に手を当て睨み付ける。
　「…私に何をした。」
　「別になにもしてないよ。ほら、続けようよ。」
　「何の話だ！？離れろ！…私は、もうこんなとこにはいたくない。ここから出してくれ…！」
