　セーレンが見つめれば、見えない手が困惑に項垂れる彼の髪を引っ張るように引き寄せられる。
　彼はぱくぱくと何事かを訴えようとしているようだったが、ついに言葉にはならず抗いきれずにちょこんと隣に座り込む。
　「ねえ、クノーテン。外はそんなに楽しいの？嬉しいの？」
　「…そうだ、嬉しいんだ。命令してもらえるんだ。…外に出ちゃダメだって。」
　まるで会話にならない。今のクノーテンはまともじゃないのだ。自分でも何がしたいのかわかっていないのだろう。過去の記憶の惰性で行動を起こしている。そうしてきっとまた、あっちの子がやってきて、繰り返し、繰り返し……
