　―それは我儘で無様な弱音だった。
　「嬉しいはずっ…ないじゃないか……私は、私だって魔法を使ってみたい。魔法で、誰かの役に立って、お願いを聞いて、わがままもたまに聞いてもらって、対等に……命令なんていやだよっ…私を見てよ……」
　「クノーテン……」
　未だにどこかを、他所を見つめるように見上げる片割れをまっすぐに見つめて、しかし不思議と怒りは沸いてこなかった。
　「…そっか、クノーテンぐちゃぐちゃなんだね。ひどいこといっぱいされたから、嫌なこともやりたいことも煩雑で、もう区別もつかないんだね……」
　「ひどい、こと…？」
　「そうだよ。いっぱいされたものね。」
　「…そんなことないよ。いっぱい命令してもらえたら嬉しいんだ。ふふ、私なんかの存在で役に立てることなら、……」
　「もういいよ、クノーテン。そんな強がりはいい。」
　「強がりなんかじゃない！」
　ぱっと払うような大きな動作と共に少年は立ち上がる。二度、三度無意味な否定の言葉を並べてから改めて強い意思を示した。
　「少なくとも、こんなとこでお前に監禁されるよりはましだ！セーレンなんて嫌いだ、もう顔もみたくない！」
