　「…失礼します。」
　緊張して張りつめた肩を覗かせてクノーテンらはおそるおそる部屋へと入っていく。スフォリアは何事かを片手間に迎え入れた。手には資料らしきものが伺える。
　「あ、あの…」
　「いつから出ていた。お前の仕業だろう、セーレン。」
　王は尋ねかけた子供には見向きもせずに後ろに佇む少年に答えを求めたが、彼は笑顔も怒りも浮かべずにただじっと片割れを見つめていた。
　「…はあ、クノーテン。説明させなさい。」
　「えっと…あの場所からは」
　「お前がする必要はない。セーレンに話すよう言いなさい」
