　クノーテンはぱっと手をかざして情景八方面を自分と王との間に映し出す。中では楽しそうにコレン達が集って飲み交わしている。魔法が使えることを証明したかったのだ。まだそのことに疑念を持っていると思っていた。けれども反応はひどく薄く、勘違いだと気づかされる。
　「何度も言わせるな。確かにセーレンは働きづめだったからな。休むのは構わない。しばらくは無しで回せるように整えなければ…」
