　「セーレンなんかが必要とされるのはそうなはずだ。お前達も魔法を使うために…そうじゃなきゃ、だって…」
　「クノーテン、大丈夫だよ。落ち着いて。」
　「うるさい触るな。気色の悪い手でオレに触れるな。」
