　「なぜ同じ力じゃいけない？セーレンなんかよりオレは素直に従ってやるよ。あんたらはオレの欲しいものを持ってる。それさえくれれば、何だって…」
　「セーレン、一体連れ出した後クノータデンスをどうしていたのだ。明らかに壊れているぞ、これは。」
