　「私は勿論、どのフィアデンもお前を創ってなどいない。ヒトビトからもコレンからの出生にも記録はない。我らは全ての生を、物の始め終わりを把握しているが、お前はその中に現れず、いつの間にかそうしてセルミアーデンに酷似した姿で隣にいたのだ。」
　「っ……そんな…」
　フィアデンではない。存在は無いに等しい。
　幾度となく教えられたそれは差別や意地悪ではなかった。―真実だった？
　「嘘だ…私は、ここにいるじゃないか。だったら私は何なんだ！？」
