export const novel = `\
　部屋には戯れ言が聞き取りにくい小声で永遠と続けられていた。自分は本当にここにいるのか、とかどこにいるのか、とか下らないことばかり。ようやく自分にもわからぬほどの自信や意思を持ち始めていたのに、他所への依存に逆戻りだ。
　セーレンは何度目かのため息をついて、少し離れて彼を見つめる。あのときからまたずっとこんな調子なのだ。話しかけても触れても自分を認識しない。いい加減耐えられなくなって口を開いた。
　「…そうじゃないよ。ダメになってるのはフィアデンの方なんだ。だからお前を認識できない。」
　「え……？わたしは…いるの…？」
　当たり前だろう、とようやく返された言葉に微笑む。
　「なに…わたしは、なに……」
　「お前はフィアデンだよ。」
　フラフラとクノーテンは立ち上がる。目は座っていない。セーレンの言葉がまだ信じられないように首をかしげている。そんな様にセーレンが近づいていって、もう一度にこりと微笑んだ。彼も微笑み返した。
　「……うそ。」
　しかしセーレンが目の前までやって来たところで、がくりと腰から上の力を抜いたように折り曲がった体勢でクノーテンは続ける。
　「うそだよ。セーレンはうそつきだね。私はフィアデンじゃないよ。……父さんが言ってたじゃないか。」
　「おかしいよ、クノーテン。なんであんなやつの言葉を鵜呑みにするのさ。」
　「うるさいよ！セーレンは嘘つきだ！父さんが正しい…父さん…が…」
　震える肩を見つめるほど、するすると侮蔑が這い上がってくる。張り付けた笑顔が消え始める。どうしてこうも、こいつは…
　「クノーテン酷いよ。だからあいつらが一体お前に何してくれたって言うんだ。何にもしない。必要としない。会話をするのだって嫌だと…」
　「それでもいい！！」
　少年はそのままの姿勢で耳をふさぐ。彼の言葉などもう聞きたくない。ハッキリと拒絶を示していた。その態度を受けて、セーレンが辛くないはずがない。ただ静かに涙が零れる。
　「ずるい…ずるいよ……なんでなの？なんであいつらは、何もしないのにお前に認められるの…？俺の方がいっぱいクノーテン好きなのに…クノーテンだって、俺が好きで堪らないはずなのに…俺は俺である以前に、俺なんだから…」
　が、そこまで言うとピタリと泣き止んでしまう。まるで始めから泣いてなんかいなかったように冷たい瞳で彼を見据えた。もう見向きもしてくれない、否定したままの、彼を。
　「…いいよ。教えてあげる。お前が何物なのか。…どうしてお前が他のすべてを好きなのか。」
　「………………」
　あのね、とセーレンはもう一度だけ返事を待った。けれども彼は相変わらず動じずに耳を塞いでいたから、無理やり腕を引きちぎって前を向かせた。
　「あのね、セーレン…クノーテン、お前は俺なんだよ？」
　暫くの間の後にようやく、は？という理性のある疑問の音が響いて、クノーテンが瞳に輝きを戻す。同時に痛みも認識したのか、固定された肩が僅かに震えた。
　「それで、俺は世界…本当の意味で、俺だけがフィアデンなんだ。……イル、とも言う。」
　「はあ…？」
　言葉の意味が何一つ、意図が全く読めないと先程までとは違うまともな否定を示すクノーテンに、セーレン目を合わせた。と。
　「……！！？…ほら、わかるでしょ。お前は俺なんだよ。」
　自らの口から発された物だと気づくのに遅れるほど、それは当たり前のことだった。
　けど。
　「…っやめろ…今、何した…？」
　信じたくない。受け入れたくなくて、ただ操られているのだと何度も自分に言い聞かせる。そんな彼に再びセーレンは同化する。今までのような実体の、ではなく、存在の同一化であった。
　「わかってるくせに。…やめろっ私から出ていけっ…出るとか出ないとかじゃないよ…嫌だ！！」
　叫びを合図に、今度はセーレンの実体が反論を始める。
　「なんでこんなことを…？！…この方が解りやすいだろ？…何、これ、セーレン？…そう、お前だよ、クノーテン」
　2つはそれまでの2つの実体へと戻ると、クノーテンが腕を取り戻して再び頭を抱えた。嘘だ嘘だと何度も呟きながら、しかし口は笑っていた。
　「だからお前はフィアデンなんだよ。俺と一緒。あいつらはフィアデンじゃないから、"どこから来たかわからない"だけでお前を認識できないんだ。」
　「なんで…？けど、私はお前じゃないじゃないか！私は別にいる…」
　「だって俺…とっても愛しいだろ？」
　何度目かの侮蔑を込めた疑問の音。
　「俺ってかわいいだろ？素敵だろう？愛しいだろう？？俺は俺を愛してるんだ。ぐちゃぐちゃにしたいし喜ばせたいし泣かせたい…でもさ、実体や存在がいっこだと、愛すのに制限ができちゃうだろ？そんなの嫌。俺は俺を好きなだけ好きなように愛したいんだ！だからお前を創った！！完全別個の俺自身を…！」
　「そんなのおかしい！……お前変だよ。壊れてる…破綻してるんだ、お前の感情は…」
　セーレンが説明する間も、否定の言葉を並べる今も、クノーテンはセーレンの顔を見なかった。見られなかった。そんなことをしたら、自分までまた壊れてしまうような気がしたから。
　「お前は…お前が壊れてるんだ、セーレン。だからそんなことを言う。そんなはずないだろ？そんなはず…」
　ずっと、同等の扱いを望んでいたはずの少年は、事実という理由でなく、ただ受け入れたくないというだけで片割れを侮辱した。
　「…俺は世界で、お前は俺だから、お前も世界そのものなんだよ。そして俺は俺が好きだから、俺である世界をお前も愛してる。けど俺は俺以外はどうでもいいから、お前は俺のことどうでもいいと感じてしまうんだ。」
　「違う、そうじゃない！」
　セーレンの説明には、おかしな前提がある。そう指摘するとゆっくりとクノーテンは両手を下げた。
　彼の言葉は自分もセーレンなのだと説得するものだ。おかしいのはそこじゃない。それも信じがたいことではあるけれど。未だに夢であればと願うけれど。
　「おかしいよ、お前…なんで、自分を愛してるんだ…」
　多くの生き物は、我が身かわいさに行動を起こす。けれども彼の自己愛は、そういうレベルの愛ではない。
　「………え？」
　「セーレン…お前は変なんだ。自覚した方がいい。お前の言う愛も、その対象も、おかしい…そんな感情は自分に向けるべきじゃない。……そっか、寂しいんだな、お前…そうだよな、お前を必要としてるやつらは、結局は根拠のある力がほしいだけだものな。だから、そんな風に自分を…」
　「黙れ。」
　ぞっとして、思わず少年は少年を見た。それまで恐れていたものはなくなっていたが、別の恐怖がクノーテンを支配する。
　「…そんなのと一緒にするな。俺は俺として俺を認識したときから俺を愛してるんだ。環境も境遇も関係ない。そんなものはいくらでも創れる飾りだ。大人しく認めろよ。お前だって俺なんだから…」
　目もそらせずに固まるクノーテンにセーレンは勢いよく飛び付く。彼が触れると、びくりとクノーテンの肩は揺れた。
　「あっ…あ……」
　愛しくて堪らなかった。今までのような調教からの反射ではない。心のそこから、自分は、彼を…自分を愛している。
　「ねえ？クノーテン…お前は、わかってくれるよね？」
　「ちがっ…こんなの…あぁっ……やっぱり、こわ、れて…る…」
　それども認めるわけにはいかない。これ以上彼に触れてはいけない。言葉を交わしてはいけない。
　頭ではそうわかっているのに、体は全くいうことを聞かなかった。セーレンを求めて、彼と身を重ねていた。
　「俺はお前がいれば何にもいらない。力も、世界も、何も…」
　「むじゅん、してるよ…お前は、世界なんだろ…？」
　「うん。とっても邪魔。そうじゃなければ、クノーテンは俺を拒絶しないのにさ。」
　「それがっ…わからない！」
　一層激しい痙攣を起こしてクノーテンは仰け反った。次の言葉は、混濁する意識をおさめてからであった。
　「お前は、世界そのもので、お前が世界を嫌ってるから、私がお前を嫌いになるのはわかる。…でもどうして、お前は私を嫌わないんだ？そもそも、自分が好きなのに、どうして世界が嫌いなんてことがおこる…？」
　驚いた。彼はすっかり自分を理解しているようだ。今までの我儘な一つ覚えの上部の否定はどうしたのだろう。
　セーレンがそんなことを考えていると、クノーテンが答えられないのかと確認する。
　「…いや、お前があまりにも簡単に受け入れるからちょっとびっくりしただけだよ。…あのね、ひとつめは勘違いだ。俺はしっかりお前のことを嫌っている。お前なんか、自分なんかだいっきらいだよ？世界が嫌いなんだから、当たり前だ。」
　セーレンは真顔のまま、クノーテンを離した手をぶらつかせて立ち上がる。
　「ふたつめも当然なんだ。世界なんて漠然とした言葉だからわかりにくいんだろうけど…世界ってのには全てのものが含まれてる。例えば、アインやスフォリアもそうだ。世界はアインだし、世界はスフォリアなんだ。…けど、アインは世界じゃないし、スフォリアも違う。それが俺との違い。俺以外は一方通行なんだ。…言うなれば他のやつらは皆世界の一部。一部が一部を嫌うこともあれば、愛すこともある。そもそも世界に好きと嫌いという概念がある時点で、俺が俺を好きでいながら嫌いになるのは当たり前のことなんだよ。」
　すらすらと彼は説明する。それまでクノーテンを愛して乱れていた姿からは想像できない、"いつもの"セーレンであった。
　「……」
　「…けど、ちょっとばかりお前は俺を嫌いすぎだ。他のやつに認められることばかりを気にしてる。理解できないよ。」
　クノーテンもすっかり冷静になっていた。自分にはわかる。どうして自分が、彼を愛しながらもそれを優先しないのか。
　「…簡単なことだよ。私はお前でありながら、お前じゃないせいだ。」
　「は？」
　そうして爽やかな笑みさえ浮かべてまっすぐに"彼"を見つめた。
　「自我ってのはさ…ユニークを望むんだ。だって、私がお前を真っ向から愛してしまったら、私はセーレンと同じになる。…そっか。だからきっと、お前に認められても満たされなかったんだな。」
　セーレンはずっと、自分を"双子"としてではなく"自分"と認めて接していたのだ。そんな彼から、唯一確かな自分が得られるはずはない。
　どういうことかと彼は不安を額に浮かべて補足を求めていた。
　「…私は、お前でないことを望んでいるんだ。愛する自分を捨ててでも。」
　少年は一層穏やかな笑顔でそう告げた。彼がその意味を理解しないはずはないと高をくくって。
　が。
　鈍い音がして、セーレンはクノーテンを押し倒す。怒りで己をいっぱいにしていた。クノーテンの理解が追い付くのも待たずに彼を捕らえていた。
　「…セー、レン？」
　「………………………」
　次第に、その怒りには困惑も混ざっていることを知った。
　「俺でないことを、望んでいる…？何言ってんの？」
　今までのような空っぽの繰り返しじゃない。彼は全てを解った上で強くそう意思を示している。それが余計にセーレンを混乱させていた。
　「な、何って…だって、そもそもお前は別の存在として私を……？！やめて、強要しないで、私はお前だけど、お前じゃないんだ…！いやだ、私を取るな…っ！？」
　「許さない。許さない許さない許さない許さない許さない許さない！！お前は俺だ！別の存在というだけで、俺イガイのなニモノデもナイ…！！」
　「やめろ！わたし、わぁ、…っ……」
　クノーテンはセーレンの勢いに、強制的な同調に虹彩を震わせ抵抗していたが、彼には勝てるはずもなく、その強引な意思で塗りつぶされる。そうして、やってきたのが…
　「……せ、れん…？ふふ、セーレンだぁ…セーレン、すきだよ、セーレン……」
　その子は、重い愛に押し潰されて自分を失った彼の姿ではなかった。
　その子は、ただ自分がすっかりセーレンであると受け入れてしまったクノーテンであったのだ。`