export const novel = `\
　鈍い音がして、また右の腕が悲鳴を上げる。痛みに泣いてもやめてくれと懇願しても、彼は蔑んで楽しむように薄く笑うばかりで話を聞いてはくれなかった。
　「ほら、クノーテン。もうわかるよね？」
　何がなにやら、いつまでたってもわからない。ただ痛くて、悲しくて、こんなことは早く終わって欲しいと願うだけであった。
　「いたい…わからない、もうやめて…セーレン……」
　「…だから俺じゃないって。」
　がんと重い石のようなもので腕がつぶされる。聞き飽きた自分の悲鳴が気持ち悪い。
　「クノーテン痛いの嫌いでしょ？悪いことするから痛くなるんだよ？…ふふ、クノーテンは俺以外に触れちゃいけないんだ。」
　「いみ、わかんな…」
　しびれたように感覚がないようで、いつまでも千切れたことを忘れられないほど痛みは続く。今はこうして腕を集中的に攻撃されていたが、始めにつぶされたのは視界だった。
　拘束の後、突然現れた…というよりは創られたコレンの羽に触られて、特に気にもならず放っておいたら魔法で左目を吹き飛ばされた。同じ要領で右目を、ようやくルールを理解した頃には左の腕も跡形もなく消え失せていた。彼は詳しく説明しなかったが、要するにセーレン以外の何かが故意に接触してきた場合には否定しろということらしい。そうしなければどこかしらを傷つけられる。しかし、何でもかんでも否定していると、たまにセーレンが触れてきて、返って酷い仕打ちを受ける。
　もはや痛覚以外の感覚はなかった。視界が戻ったとして、何が触れてきたかなどわかるはずもない。
　「むりだよ！わかんない…」
　「わかるよ。クノーテンは俺以外に触られたくないもんね。」
　何の冗談なのか、何かの訓練なのか。
　ただでさえ嫌な事続きで参っているというのに、どうしてこのようなことを、しかも今までずっと唯一の味方のはずであった彼にされなければならないのか。
　「せーれん…私、疲れてるの。せめて…寝かせて。」
　「勿論、これが終わったらね。ほら、これは何が触ってるかな…？」
　幾ら考えても、いや、もう考えることもまともにできはしないのだ。遠回しに目的を果たそうとするにしても、あまりにも効率が悪い。
　黙り込んでいたから、またぶたれる。
　「ダメだよ、クノーテン。頭で考えるからわからないんだ。感じればいいんだよ。…ほら、俺が触ると嬉しいでしょ？興奮するだろ？」
　何を言っているのだと未だ見えない目で方向だけ合わせる。と、正面から顔をしっかりと捕まれて直接悦を注入された。
　「＃％”＋！＊｀P#$"？！」
　並には受け止めきれない膨大な快楽がこの身を駆け抜ける。突き落とされた死体のように飛びはね、やがて静かにその場へ転がった。
　「これが俺だよ、クノーテン……」
　「……………」
　戻りかけた視界に、ぼやけて自分と同じ微笑み顔が見えた。
　
　あれ程疲れていたというのに、解放されてしばらくした今でも上手く眠れなくて、むしろ冴えた頭の中に色々なことがぼーと掛け巡るのは、セーレンが体を密着させたまま眠ってしまったせいだ。たったそれだけで、びくびくと体が興奮のためいうことをきかなくなる。
　あれからそのような状態となるまで、そう時間は要さなかった。
　隣で眠るきょうだいは、先程までの闇を潜め心地よさそうに静かだ。
　結局その行為への理由は聞き出せていない。が、思い出せばセーレンは”掃除”だと言っていた。単に記憶を消すだとか、辛い物を吐き出そう、などではなく。掃除とは、また汚れる可能性があるから、一度綺麗にしておくという行為だ。
　それは、つまり――
　そこまで考えて、少年は首を振った。違う。今考えるべきはそんなことではない。どうにかしてここを抜け出すべきだ。もうあの地獄からは逃れられた。後は戻るだけだ。後は……
　無論自分勝手に対する後ろめたさがないわけではない。動機や原因はともかく、今回の事態を決めたのはセーレンではない。むしろ彼は必死に感情を抑え、一族を裏切って攫ってまで助けようとしてくれた。そのことには確かに感謝をしている。けれども。
　―私が望んでいるのは、こんな場所じゃない。
　起こさないようにそっと、表層の欲望に抗って布団を抜け出す。クノーテンがルールを体で理解したためか、すでに拘束は解かれてふんわりとした布に包まれていた。
　一つ伸びをして凝り固まった体をほぐす。終わり際手を下げながら辺りを見渡すが、どこにも扉はなかった。こうなると、彼にはどうしようもない。
　クノーテンはもう一度セーレンを見る。あんなにも気軽に話しかけられていたのが嘘みたいに、うまく声がかけられない。それは、気持ちよさそうに眠る子を起こしたくはないなどと言う優しさからではなく。
　…ずっと、ひとつの嫌な予感がある。何度拭い去っても消えずに輝き続ける可能性。とても、とても嫌な……
　
　―逃げて、どうなるというのだ。
　ふと、そんな言葉が頭をよぎった。それはなんとか決心をつけて「ここから出して欲しい。」と彼にお願いしようと声を出す手前であった。思い出してしまった情景から連想されるこれからが、冷静になったのかあまりにも悲惨であったのだ。それは明るい花畑などではない。
　自分に居場所はない。フィアデンは考えるまでもないが、彼らが導くと息巻き、また彼らを迷いなく慕うヒトビトやコレンも今ではどのような知恵を与えられているのかわからない。とにかくあんな場所から出られればそれまでの状態が待っていると思っていたが、本当に”そのまま”でいてくれているのだろうか。…光塊のこども達に、慰みと化した自分という存在の記憶が必要か？
　父は…王はフィアデン、生き物全てから慕われるのが納得できるほど優しくて善い君主だ。種族など関係ない。できないを補うのみで、見下したり驕るようなことなどしない。不要な、それどころかマイナスになりかねない記憶など、ない方が皆のためになる。だから彼は、きっと何の迷いもなく。
　クノーテンの価値など、その程度であった。少なくとも彼自身はそう確信していた。普段のあの優しさは、自分を前にすると影も形もなく辛辣な対応をする。そして王がそうであったからこそ、きょうだいとして育ったアインやセーレン以外のどのフィアデンもが彼をそのように扱うことに疑問を持たなかった。
　悪いのは自分が存在すること。それをどうしようが、構わない。
　噛み締めた口の代わりに目元が熱くなる。どんな快楽を与えられても、安全を保証されても、決して満たされはしない。そう、私が欲しいのはそんなものじゃない。見せかけで偽りで、その場しのぎの喜びなんかじゃ足りない。
　「泣いてる……？」
　弱々しい自分の声に驚いて顔を上げる。それはよく似た双子の声であった。彼はいつも通りの大人しい佇まいで座っていた。少年の驚きはその、突然の音と彼の彼らしさからくるものだったが、片割れはそうとらなかったのかゆっくりと歩みを進めてクノーテンの頬に手を伸ばした。わずかに肩が震えたが、彼も大人しいものであった。
　「クノーテン、悲しいの？」
　「…ああ、空しい。私は…必要がない。」
　うつろな瞳にほんの少し嫌疑の光が映る。そんなことを言ったらまた、おかしくなって自分だけ居ればいいだろうだとか締め付けられるのではと思い出したからであった。が、彼はそれ以上妖しい言動は見せなかった。
　「どうして泣いているの…？」
　だから、とあふれたのは言葉よりも涙だった。目の下に溜まっていたものがあふれ出して、力が抜けて膝を折る。ただただ、悲しかった。当たり前のことが、日常が、帰りたいはずだった場所が、悲しかった。
　「ちがう……私はあそこに帰りたいんだ。慰めるための小部屋でも、お前の創った世界でもない。日常に……」
　「なぜ？ここは素敵なところだよ。何でもあげる。友達も、家族も、クノーテンの欲しいものは全部あげる。…痛いのが嫌なら、我慢するから。」
　「ちがう…ちがうんだ、セーレン。」
　クノーテンはゆっくりと首を横に振る。そうしてようやくセーレンが怒りを表情に見せた。
　彼らの間に彼の意思とは関係なくクノーテンの腕が突き出される。それは奇怪な模様が支配する、左の腕であった。
　「こんなことされて、まだあいつを慕うか…！スフォリアは、お前から全てを奪う…」
　彼は強制力よりも言葉に目を丸めてそれは…と一度は口ごもり戸惑っていたが、
　「前に剥がそうとしてただろ！？存在のウチガワまで念入りに染み込んでいるって…あのときも、泣いていた。…だったら、俺が」
　「やめろ！」
　セーレンの思惑を読んで右の腕で庇うように左手を包み、また首を振る。セーレンの歯ぎしりがやけに響いた。が、クノーテンの悲哀に満ちた笑顔に今にも逆立つように揺らいでいた彼の髪は落ち着いてしまう。
　「消さないで…だめだろ、セーレン……」
　そんな顔で自分の名を呼ばれては、もうどうしようもないではないか。
　「なんで……だって、」
　「ずっと剥がしたいって、思ってた。でもだめだ。今ここでこれを失くしたら、何も残らなくなる。あの方達との繋がりが、何も…ひとつになっちゃうよ。」
　「……なんでそんなこと気にするんだ。おかしいよ、クノーテン。」
　クノーテンにもわかっている。おかしいのはもう彼じゃない。怒るのが普通だ。怒りにまかせて家出をして、ひとつで己が道を進むなり、新たな仲間を作るのが。けれども辛辣な態度や扱いまでもがなくなったら、私は私をどう認識すればいい―？
　「泣かないでよ、笑うなよ。あいつらのせいで。…そんなの、そんなの…ずるい、よ。あいつらは何もしないのに。お前のためにならないのに。それなのに。」
　クノーテンの視界に不思議な景色が映った。涙に歪んだ幻なのかと拭ってみたが、それは目の前に確かにあったのだ。…セーレンが、彼のその泣き顔はきっとこの世界の何よりも”美しかった”。もしかしたら、共感して悲しんでもらえることがあまりにも珍しくて、嬉しさに錯覚しただけかもしれないが、そのときのクノーテンを再び動揺させるには十分であった。
　「せー…レン？」
　「俺はクノーテンと一緒に居たいのに。ただクノーテンと、いられればそれでいいから、何でもするのに…クノーテンは外ばかりを見ているんだ。」
　本当に彼はどうしてこう、自分に拘るのだろうか…？自分こそ、彼に何もしてやれることはない。必要とされる意味が分からない。
　「…いいんだ、もう。私のことなんか放っておいてくれ。」
　「クノーテンは何も解ってない！」
　「わからないよ！！…もうやめてくれ。私を求めるな。私には何もできない。」
　自分の居場所だってろくに得られない。選択肢を与える前に失望させる。
　「そうだ。もうおかしくなってたんだ。ここで客観視してようやくわかった。…っふ、あはは、はははっはは……あはははっ！！」
　「く、クノーテン！？どうしたの、しっかりして。」
　壊れたように笑い続けるクノーテンにセーレンも動揺する。笑っている理由が皆目見当もつかなかった。どうすれば彼が落ち着いてくれるのだろうか。そんなことさえ考えられずに、揺れる自分の心に驚いて、ぎゅっとクノーテンにしがみつく。
　「クノーテン、やだよ、こわれちゃ嫌……」
　逃げようとする彼の意識を見つけ出して離れぬようしっかり掴んで無理にでも引き戻す。彼の心情ではそんな強制的な行動が精一杯だった。
　意識を叩き起こされてクノーテンはキッと片割れを睨んだ。まるで余計なお世話だと、このまま眠って消えてしまいたいと言わんばかりにわずかに残った正常な感情を怒りに変えた。
　
　どうして彼がそこまで求めるのかはわからない。けれども彼がそれを求める限り、自分ではどうしようもない欲望であると、痛いほど理解していた。`