

























import {Vue, Component, Prop, Watch} from 'vue-property-decorator';
import {ContentType, IllustInfo, ComicInfo, NovelInfo} from '@/model/itemInfo.ts';
import TemplateListOnly from '@/components/template-list-only.vue'
import DialogIllust from '@/components/dialog-illust.vue';
import DialogComic from '@/components/dialog-comic.vue';
import DialogNovel from '@/components/dialog-novel.vue';
export type itemType = IllustInfo | ComicInfo | NovelInfo;

@Component({
  components:{
    TemplateListOnly,
    DialogIllust,
    DialogComic,
    DialogNovel
  }
})
export default class TemplateListWithDialog extends Vue{
  @Prop()
  items!: itemType[];
  @Prop()
  isInside = false;
  @Prop()
  existNovel!: boolean
  @Prop()
  existComic!: boolean

  private isVertical = false;
  private touchMode = false;
  private current = this.items[0];

  clickList(index: number){
    this.current = this.items[index];
    this.current.show = !this.current.show
  }
  checkType(item: itemType, type: number){
    switch(type){
      case 0:
        return item.type === ContentType.ILLUST;
      case 1:
        return item.type === ContentType.COMIC;
      case 2:
        return item.type === ContentType.NOVEL;
      default:
    }
  }
}

